g.type-se > circle {
  fill: #00ffd0;
}

text >tspan {
  text-decoration-color: red;
  font-weight: 300;
  font-family: "Helvetica Neue", Helvetica, Arial, sans-serf;
  font-size: 14px;
}

.node rect {
  stroke: #999;
  fill: #fff;
  stroke-width: 1.5px;
}

.node {
  cursor: pointer;
}

.edgePath path {
  stroke: #333;
  stroke-width: 1.5px;
  cursor: pointer;
}